  /* Clears unordered list default of 40px */ }
  body #grid .row {
    display: flex;
    flex-wrap: nowrap; }
    body #grid .row * {
      flex-shrink: 0; }

body #grid li {
  list-style-type: none;
  position: relative;
  float: left;
  width: 150px;
  padding: 0 0 calc(1.1547*150px) 0;
  -webkit-transform: rotate(-60deg) skewY(30deg);
  transform: rotate(-60deg) skewY(30deg);
  overflow: hidden; }

body #grid li .hexagon {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-transform: skewY(-30deg) rotate(60deg);
  transform: skewY(-30deg) rotate(60deg);
  overflow: hidden;
  animation: hue 6s infinite alternate-reverse;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out; }

body #grid li:nth-child(n) {
  margin: 0 7px; }

body #grid li:nth-child(6n+4) {
  margin-left: 5px; }

body #grid .row:nth-child(2n) li {
  margin-top: -32px;
  margin-bottom: -32px;
  transform: translateX(56%) rotate(-60deg) skewY(30deg);
  -o-transform: translateX(56%) rotate(-60deg) skewY(30deg);
  -moz-transform: translateX(56%) rotate(-60deg) skewY(30deg);
  -webkit-transform: translateX(56%) rotate(-60deg) skewY(30deg);
  -ms-transform: translateX(56%) rotate(-60deg) skewY(30deg); }

